.calculator-button {
    position: absolute;
    border: none;
    background-color: transparent;
    width: 30px;
    height: 30px;
    cursor: pointer;
    outline: none;
    padding: 0;
    top: 10px;
    left: 10px;
}

.calculator-button img {
    width: 100%;
    height: 100%;
}